/* General App styles */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New styles for centering the content and adding margin to the left */
.center-content {
  text-align: center;
  margin-bottom: 20px;
}

/* Top navigation styling */
.top-nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

/* Pulse Animation for Links */
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    color: #61dafb; /* Initial color */
  }
  50% {
    transform: scale(1.1); /* Slightly grow */
    color: #4ecdc4; /* Change color for a pulsing effect */
  }
}

/* Apply the pulse effect to each navigation link */
.nav-link {
  color: #61dafb; /* Base color */
  text-decoration: none;
  font-size: 1.2em;
  font-weight: bold;
  animation: pulse 2s infinite; /* Pulse effect */
  transition: transform 0.3s ease; /* Smooth transition */
}

.nav-link:hover {
  color: #ff6b6b; /* Hover color */
  transform: scale(1.15); /* Slightly larger on hover */
}

/* Add margin on the left for other sections */
.left-space {
  margin-left: 20px;
}

/* Specific image sizing for each section */
.academic-image {
  width: 300px;
  height: auto;
  margin-bottom: 20px;
}

.work-image {
  width: 300px;
  height: auto;
  margin-bottom: 20px;
}

.research-image {
  width: 400px;
  height: auto;
  margin-bottom: 20px;
}

.ai-image {
  width: 400px;
  height: auto;
  margin-bottom: 20px;
}

/* Consistent layout across each page */
.page-content {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

/* PDF list styling for Academics */
.pdf-list {
  list-style-type: none; /* Remove bullet points */
  padding: 0; /* Remove default padding */
}

.centered-pdf-list {
  text-align: center; /* Center the list */
}

.centered-pdf-list li {
  margin-bottom: 10px; /* Add space between items */
}

.centered-pdf-list a {
  color: #1605f7; /* Link color */
  text-decoration: underline; /* Add underline to make it clear it's a link */
  font-size: 1rem; /* Adjust font size */
  font-weight: bold; /* Make the text slightly bolder */
  transition: color 0.2s ease, transform 0.2s ease; /* Smooth transition for hover */
}

.centered-pdf-list a:hover {
  color: #ff6b6b; /* Hover color */
  text-decoration: underline; /* Underline on hover */
}


/* Simplified .home-page styling (background image is set inline in Home.js) */
.home-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 20px;
}

/* Style for the back button */
.back-button {
  background-color: #0056b3; /* Blue color */
  color: white; /* White text */
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.back-button:hover {
  background-color: #003d80; /* Darker blue on hover */
  transform: scale(1.05); /* Slight zoom effect */
}

.back-button:active {
  transform: scale(1); /* Reset scale on click */
}
